import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import createToken from 'src/services/create-token';
import retrieveUserData from 'src/services/retrieve-user-data';
import { API_STATUS, ERROR_MESSAGES } from 'src/static/api';
import { formatDDMMMYYYY } from 'src/utils/date';
import { prepareBody, updateStatusForUser } from 'src/utils/issuance-summary';
import { getId, getNormalizedUserData, getUserData, getUserDataByBookingId } from 'src/utils/mobile-key-status';

const initialState = {
  mobileKeyStatusList: [],
  mobileKeyStatusById: {},
  issuanceSummaryById: {},
  status: API_STATUS.initial,
  errorMessage: ERROR_MESSAGES[500],
};

export const fetchUserData = createAsyncThunk('mobileKeyStatus/fetchUserData', async () => {
  try {
    const currentDate = formatDDMMMYYYY(new Date());
    const token = await createToken();
    const response = await retrieveUserData({ token, currentDate });
    if (response.UserDetailsList && response.UserDetailsList.length > 0) {
      return response.UserDetailsList;
    }
    return [];
  } catch (err) {
    throw new Error(err.message);
  }
});

export const updateStatusById = createAsyncThunk(
  'mobileKeyStatus/updateStatusById',
  async ({ mobileKeyStatusIndex, status, id, row, rows, bookingId }) => {
    const requestBody = prepareBody({ status, id, row, rows, bookingId });
    await updateStatusForUser({ requestBody });
    return { status, id, mobileKeyStatusIndex };
  }
);

export const mobileKeyStatusSlice = createSlice({
  name: 'mobileKeyStatus',
  initialState,
  reducers: {
    updateLoadingStatus: (state, action) => {
      state.status = action.payload;
    },
    updateMobileKeyStatus: (state, action) => {
      const { mobileKeyStatusList, mobileKeyStatusById, issuanceSummaryById } = action.payload;
      state.issuanceSummaryById = issuanceSummaryById;
      state.mobileKeyStatusList = mobileKeyStatusList;
      state.mobileKeyStatusById = mobileKeyStatusById;
    },
    updateMobileKeyStatusById: (state, action) => {
      const { mobileKeyStatusById, issuanceSummaryById } = action.payload;
      state.mobileKeyStatusById = { ...state.mobileKeyStatusById, ...mobileKeyStatusById };
      state.issuanceSummaryById = { ...state.issuanceSummaryById, ...issuanceSummaryById };
    },
    updateStatus: (state, action) => {
      const { mobileKeyStatusIndex, status, id, approverId, approvalTime } = action.payload;
      const issuanceSummaryId = getId(mobileKeyStatusIndex, id);
      state.issuanceSummaryById[issuanceSummaryId].approvalStatus = status;
      state.issuanceSummaryById[issuanceSummaryId].approvedBy = approverId;
      state.issuanceSummaryById[issuanceSummaryId].approvedTime = approvalTime;
    },
    updateComments: (state, action) => {
      const { mobileKeyStatusIndex, comments, emailId } = action.payload;
      const issuanceSummaryId = getId(mobileKeyStatusIndex, emailId);
      state.issuanceSummaryById[issuanceSummaryId].comments = comments;
    },
    updateErrorMessage: (state, action) => {
      state.errorMessage = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(fetchUserData.pending, (state) => {
        state.status = API_STATUS.loading;
        state.errorMessage = '';
      })
      .addCase(fetchUserData.fulfilled, (state, action) => {
        state.status = API_STATUS.done;
        const { mobileKeyStatusList, mobileKeyStatusById, issuanceSummaryById } = getNormalizedUserData(action.payload);
        state.issuanceSummaryById = issuanceSummaryById;
        state.mobileKeyStatusList = mobileKeyStatusList;
        state.mobileKeyStatusById = mobileKeyStatusById;
        state.errorMessage = '';
      })
      .addCase(fetchUserData.rejected, (state, action) => {
        state.status = API_STATUS.failed;
        state.errorMessage = action.payload;
      })
      .addCase(updateStatusById.pending, (state) => {
        state.status = API_STATUS.loading;
      })
      .addCase(updateStatusById.fulfilled, (state, action) => {
        state.status = API_STATUS.done;
        const { mobileKeyStatusIndex, status, id } = action.payload;
        const issuanceSummaryId = getId(mobileKeyStatusIndex, id);
        state.issuanceSummaryById[issuanceSummaryId].approvalStatus = status;
      })
      .addCase(updateStatusById.rejected, (state) => {
        state.status = API_STATUS.failed;
      });
  },
});

export const mobileKeyStatusSelector = (state) =>
  state.mobileKeyStatus.mobileKeyStatusList.map((id) => state.mobileKeyStatus.mobileKeyStatusById[id]);
export const {
  updateLoadingStatus,
  updateMobileKeyStatus,
  updateMobileKeyStatusById,
  updateStatus,
  updateErrorMessage,
  updateComments,
} = mobileKeyStatusSlice.actions;

export const fetchUserDataDispatch = () => (dispatch) => {
  try {
    dispatch(updateLoadingStatus(API_STATUS.loading));
    const currentDate = formatDDMMMYYYY(new Date());
    getUserData({ currentDate }).then((response) => {
      const data = getNormalizedUserData(response);
      dispatch(updateMobileKeyStatus(data));
      dispatch(updateLoadingStatus(API_STATUS.done));
    });
  } catch (err) {
    dispatch(updateLoadingStatus(API_STATUS.failed));
    dispatch(updateErrorMessage(err.message));
  }
};

export const updateStatusDispatch = (data) => (dispatch) => {
  try {
    getUserDataByBookingId({ bookingId: data.bookingId }).then((response) => {
      const mobileKeyStatus = getNormalizedUserData(response);
      dispatch(updateMobileKeyStatusById(mobileKeyStatus));
    });
  } catch (err) {
    dispatch(updateLoadingStatus(API_STATUS.failed));
    dispatch(updateErrorMessage(err.message));
  }
};

export default mobileKeyStatusSlice.reducer;
