import { API_METHODS, API_ROUTES } from 'src/static/api';
import { formatDDMMMYYYY } from 'src/utils/date';
import createToken from './create-token';
import { getBearerAuthorizationHeader, getHeaders } from './utils';

const getBody = ({ bookingId }) => {
  return {
    bookingId,
  };
};

const retrieveUserDataByBookingId = async ({ token, bookingId }) => {
  try {
    const body = getBody({ bookingId });
    const partialHeaders = getHeaders();
    const authorizationHeader = getBearerAuthorizationHeader(token);
    const headers = { ...partialHeaders, ...authorizationHeader };
    const response = await fetch(API_ROUTES.retrieveUserDataByBookingId, {
      method: API_METHODS.post,
      headers,
      body: JSON.stringify(body),
    });
    if (response.status === 401) {
      localStorage.removeItem('access_token');
      await createToken();
      const accessToken = localStorage.getItem('access_token');
      await retrieveUserDataByBookingId({
        token: accessToken,
        currentDate: formatDDMMMYYYY(new Date()),
      });
    } else if (response.status === 200) {
      const responseJson = await response.json();
      return responseJson;
    } else {
      throw new Error(`${response.status} : ${response.statusText}`);
    }
    return '';
  } catch (err) {
    throw new Error(err.message);
  }
};

export default retrieveUserDataByBookingId;
