import { Box, Button, Divider, Grid, Stack, TextField, Typography } from '@mui/material';
import { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { updateLoadingStatus } from 'src/redux/transfer-document';
import { transportDocumentSelector } from 'src/redux/transport-document';
import { API_STATUS } from 'src/static/api';
import { FONT_FAMILIES } from 'src/static/styles';
import { TRANSPORT_DOCUMENT_HEADERS } from 'src/static/transport-document';
import APIAlert from '../shared/api-alert';
import CustomLoader from '../shared/loader';
import SearchDocument from './search-document';

function CustomDivider() {
  return <Divider orientation='vertical' flexItem sx={{ borderColor: 'neutral.500' }} />;
}

function Item({ header, value, textFieldEnabled = false }) {
  const [currentValue, setCurrentValue] = useState('');

  const handleChange = (e) => {
    setCurrentValue(e.target.value);
  };
  return (
    <Box>
      <Typography
        variant='body2'
        sx={{
          fontWeight: 'bold',
          fontFamily: FONT_FAMILIES.gilroy,
        }}
        component='div'
      >
        {header}
      </Typography>
      {textFieldEnabled ? (
        <Stack direction='row' spacing={2}>
          <TextField
            sx={{
              fontFamily: FONT_FAMILIES.gilroy,
            }}
            fullWidth
            size='small'
            disabled={false}
            value={currentValue}
            onChange={handleChange}
            variant='outlined'
          />
          <Button
            sx={{
              fontFamily: FONT_FAMILIES.gilroy,
            }}
            variant='contained'
            size='small'
            onClick={() => {}}
          >
            Send
          </Button>
        </Stack>
      ) : (
        <Typography
          variant='body2'
          sx={{
            fontFamily: FONT_FAMILIES.gilroy,
          }}
          component='div'
        >
          {value}
        </Typography>
      )}
    </Box>
  );
}

function TransportGrid({ header = 'Pick Up' }) {
  return (
    <Box
      sx={{
        mt: 2,
      }}
    >
      <Box
        sx={{
          width: '100%',
          mb: 2,
          px: 3,
          backgroundColor: 'neutral.500',
        }}
      >
        <Typography
          variant='subtitle2'
          sx={{
            color: 'text.tertiary',
            fontFamily: FONT_FAMILIES.gilroy,
          }}
        >
          {header}
        </Typography>
      </Box>
      <Grid container spacing={2} sx={{ mt: 2 }}>
        <Grid item xs>
          <Stack spacing={2}>
            <Item header={TRANSPORT_DOCUMENT_HEADERS.date} value='---' />
            <Item header={TRANSPORT_DOCUMENT_HEADERS.station} value='---' />
            <Item header={TRANSPORT_DOCUMENT_HEADERS.notes} value='---' />
          </Stack>
        </Grid>
        <CustomDivider />
        <Grid item xs>
          <Stack spacing={2}>
            <Item header={TRANSPORT_DOCUMENT_HEADERS.time} value='---' />
            <Item header={TRANSPORT_DOCUMENT_HEADERS.carrier} value='---' />
          </Stack>
        </Grid>
        <CustomDivider />
        <Grid item xs>
          <Stack spacing={2}>
            <Item header={TRANSPORT_DOCUMENT_HEADERS.type} value='---' />
          </Stack>
        </Grid>
        <CustomDivider />
        <Grid item xs>
          <Stack spacing={2}>
            <Item header={TRANSPORT_DOCUMENT_HEADERS.transportNumber} value='---' />
            <Item header={TRANSPORT_DOCUMENT_HEADERS.confirmationNumber} value='---' textFieldEnabled />
          </Stack>
        </Grid>
      </Grid>
    </Box>
  );
}

function TransportDocument() {
  const { status, errorMessage } = useSelector(transportDocumentSelector);
  const dispatch = useDispatch();
  return (
    <>
      {status === API_STATUS.initial && <SearchDocument />}
      {status === API_STATUS.failed && <p>{errorMessage}</p>}
      {status === API_STATUS.done && (
        <Box
          sx={{
            width: '100%',
            mb: 2,
            px: 3,
          }}
        >
          <Box
            sx={{
              width: '100%',
              mb: 2,
              px: 3,
              backgroundColor: 'secondary.dark',
            }}
          >
            <Typography
              variant='h6'
              sx={{
                color: 'text.tertiary',
                fontFamily: FONT_FAMILIES.gilroy,
              }}
            >
              Transport
            </Typography>
          </Box>
          <TransportGrid header='Pick Up' />
          <TransportGrid header='Drop Off' />
        </Box>
      )}
      <CustomLoader loadingStatus={status} />
      <APIAlert
        loadingStatus={status}
        onClose={() => {
          dispatch(updateLoadingStatus(API_STATUS.initial));
        }}
        errorMessage={errorMessage}
        successMessage=''
      />
    </>
  );
}

export default TransportDocument;
