import { Box, Card, CardContent, Container } from '@mui/material';
import TransportDocument from 'src/components/transport-document';

function Transport() {
  return (
    <Box
      component='main'
      sx={{
        flexGrow: 1,
        py: 8,
      }}
    >
      <Container maxWidth={false}>
        <Card>
          <CardContent>
            <TransportDocument />
          </CardContent>
        </Card>
      </Container>
    </Box>
  );
}

export default Transport;
