import { APPROVER_ID, GUEST_TYPES, REQUESTED_STATUS_TYPES } from 'src/static/mobile-key-status';
import { getToken } from 'src/services/create-token';
import sendEmail from 'src/services/send-email';
import updateComments from 'src/services/update-comments';
import updatePrimaryStatus from 'src/services/update-primary-status';
import { formatddMMyyyy24Hours } from './date';

export const getPrimaryEmailId = (rows) => {
  let primaryEmailId = '';
  const row = rows.find((data) => data.guestType === GUEST_TYPES.primary);
  if (row && row.emailId) {
    primaryEmailId = row.emailId;
  }
  return primaryEmailId;
};

export const getRequestBody = (row) => {
  let requestBody = {
    firstName: '',
    lastName: '',
    bookingId: '',
    emailId: '',
    hotelCode: '',
    hotelName: '',
    endDate: '',
    beginDate: '',
    cardNumber: '',
    langId: '',
    deviceId: '1',
    roomNumber: '',
    primaryMemberId: '',
  };
  if (row.requestBody) {
    requestBody = JSON.parse(row.requestBody);
    delete requestBody.guestEmailId;
    delete requestBody.emailId;
  }
  return requestBody;
};

export const prepareBody = ({ row, status, id, rows, bookingId, approverId = APPROVER_ID }) => {
  let body = {};
  const requestBody = getRequestBody(row);
  if (row.guestType === GUEST_TYPES.primary && row.approvalStatus === REQUESTED_STATUS_TYPES.approvalPending) {
    body = {
      bookingId: bookingId || '',
      emailId: id,
      memberId: row.memberId,
      roomNumber: row.roomNumber,
      tokenId: row.tokenId,
      approvalStatus: status,
      approvalTime: formatddMMyyyy24Hours(new Date()),
      approverId,
    };
  } else if (status === REQUESTED_STATUS_TYPES.approved) {
    const primaryEmailId = getPrimaryEmailId(rows);
    body = {
      ...requestBody,
      primaryEmailId,
      secondaryEmailId: id,
      approvalStatus: status,
      approvalTime: formatddMMyyyy24Hours(new Date()),
      approverId,
    };
  } else {
    body = {
      approvalStatus: status,
      secondaryEmailId: id,
      approvalTime: formatddMMyyyy24Hours(new Date()),
      approverId,
      bookingId: bookingId || '',
    };
  }

  return body;
};

export const updateStatusForUser = async ({ guestType, requestBody, previousStatus }) => {
  try {
    const token = await getToken();
    if (guestType === GUEST_TYPES.primary && previousStatus === REQUESTED_STATUS_TYPES.approvalPending) {
      await updatePrimaryStatus({ token, requestBody });
    } else {
      await sendEmail({ token, requestBody });
    }
  } catch (err) {
    throw new Error(err);
  }
};

export const updateCommentsForUser = async ({ requestBody }) => {
  try {
    const token = await getToken();
    await updateComments({ token, requestBody });
  } catch (err) {
    throw new Error(err.message);
  }
};
