import { useState } from 'react';
import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField } from '@mui/material';
import { formatYYYYMMDDWith12hoursAMPM } from 'src/utils/date';
import { API_STATUS, ERROR_MESSAGES, SUCCESS_MESSAGES } from 'src/static/api';
import { MOBILE_KEY_ISSUANCE_SUMMARY_TABLE_HEADER, REQUESTED_STATUS_TYPES } from 'src/static/mobile-key-status';
import { prepareBody, updateCommentsForUser, updateStatusForUser } from 'src/utils/issuance-summary';
import { useDispatch, useSelector } from 'react-redux';
import { updateComments, updateStatusDispatch } from 'src/redux/mobile-key-status';
import { userSelector } from 'src/redux/auth';
import APIAlert from 'src/components/shared/api-alert';
import CustomLoader from 'src/components/shared/loader';
import RequestedStatusCol from './requested-status-col';
import AssignStatus from './assign-status';

function SummaryTable({ rows = [], mobileKeyStatusIndex, bookingId }) {
  const dispatch = useDispatch();
  const [loadingStatus, setLoadingStatus] = useState(API_STATUS.initial);
  const [successMessage, setSuccessMessage] = useState(SUCCESS_MESSAGES.status);
  const [errorMessage, setErrorMessage] = useState(ERROR_MESSAGES[500]);
  const [currentComments, setCurrentComments] = useState('');
  const [currentIndex, setCurrentIndex] = useState(undefined);
  const { emailId } = useSelector(userSelector);

  const handleSubmitStatus = async ({ status, id, row }) => {
    try {
      setLoadingStatus(API_STATUS.loading);
      const requestBody = prepareBody({ status, id, row, rows, bookingId, approverId: emailId });
      await updateStatusForUser({ guestType: row.guestType, requestBody, previousStatus: row.approvalStatus });
      dispatch(updateStatusDispatch({ mobileKeyStatusIndex, id, status, ...requestBody, bookingId }));
      setSuccessMessage(SUCCESS_MESSAGES.status);
      setLoadingStatus(API_STATUS.done);
    } catch (err) {
      setLoadingStatus(API_STATUS.failed);
      setErrorMessage(err.message);
    }
  };

  const handleCommentSubmit = async ({ comments, emailId: currentEmailId }) => {
    try {
      setLoadingStatus(API_STATUS.loading);
      await updateCommentsForUser({ requestBody: { comments, emailId, bookingId } });
      dispatch(updateComments({ mobileKeyStatusIndex, comments, emailId: currentEmailId }));
      setSuccessMessage(SUCCESS_MESSAGES.comments);
      setLoadingStatus(API_STATUS.done);
    } catch (err) {
      setLoadingStatus(API_STATUS.failed);
      setErrorMessage(err.message);
      setCurrentComments('');
    }
  };

  return (
    <Paper elevation={12}>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 650 }} aria-label='simple table'>
          <TableHead>
            <TableRow>
              {MOBILE_KEY_ISSUANCE_SUMMARY_TABLE_HEADER.map(({ label, id }) => {
                return <TableCell key={id}>{label}</TableCell>;
              })}
            </TableRow>
          </TableHead>
          <TableBody>
            {rows.map((row, index) => (
              <TableRow key={row.emailId}>
                <TableCell
                  sx={{
                    width: '4%',
                  }}
                >
                  {index + 1}
                </TableCell>
                <TableCell
                  align='right'
                  sx={{
                    width: '16%',
                  }}
                >
                  {row.emailId}
                </TableCell>
                <TableCell
                  sx={{
                    width: '7%',
                  }}
                >
                  {row.guestType}
                </TableCell>
                <TableCell
                  sx={{
                    width: '14%',
                  }}
                >
                  {formatYYYYMMDDWith12hoursAMPM(row.requestedOn)}
                </TableCell>
                <TableCell
                  sx={{
                    width: '12%',
                  }}
                >
                  {row.approvalStatus === REQUESTED_STATUS_TYPES.approvalPending ? (
                    <AssignStatus id={row.emailId} row={row} handleSubmitStatus={handleSubmitStatus} />
                  ) : (
                    <RequestedStatusCol
                      status={row.approvalStatus}
                      id={row.emailId}
                      row={row}
                      handleSubmitStatus={handleSubmitStatus}
                    />
                  )}
                </TableCell>
                <TableCell
                  sx={{
                    width: '14%',
                  }}
                >
                  {formatYYYYMMDDWith12hoursAMPM(row.approvedTime)}
                </TableCell>
                <TableCell
                  sx={{
                    width: '9%',
                  }}
                >
                  {row.approvedBy}
                </TableCell>
                <TableCell
                  sx={{
                    width: '24%',
                  }}
                >
                  <TextField
                    size='small'
                    fullWidth
                    multiline
                    aria-label='comment'
                    placeholder='comments...'
                    value={currentIndex !== undefined && currentIndex === index ? currentComments : row.comments}
                    style={{ width: 200 }}
                    onFocus={() => setCurrentIndex(index)}
                    onChange={(event) => {
                      setCurrentComments(event.target.value);
                    }}
                    onBlur={() => {
                      const value = currentComments.trim();
                      if (value.length > 0) {
                        handleCommentSubmit({ comments: value, emailId: row.emailId });
                      }
                      setCurrentComments('');
                      setCurrentIndex(undefined);
                    }}
                  />
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
      <CustomLoader loadingStatus={loadingStatus} />
      <APIAlert
        loadingStatus={loadingStatus}
        onClose={() => setLoadingStatus(API_STATUS.initial)}
        errorMessage={errorMessage}
        successMessage={successMessage}
      />
    </Paper>
  );
}

export default SummaryTable;
