import { Button, FormLabel, Grid, Stack, TextField, Typography } from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import { getDocument, setBookingId, transportDocumentSelector } from 'src/redux/transport-document';
import { FONT_FAMILIES } from 'src/static/styles';

function SearchDocument() {
  const { bookingId } = useSelector(transportDocumentSelector);
  const dispatch = useDispatch();

  const handleChange = (event) => {
    dispatch(setBookingId(event.target.value.trim()));
  };

  return (
    <Grid container spacing={2}>
      <Grid item xs={3}>
        <Stack spacing={2} sx={{ m: 1 }}>
          <Typography variant='h5'>Transport Document</Typography>
          <FormLabel>Enter Booking Id : </FormLabel>
          <TextField
            sx={{
              fontFamily: FONT_FAMILIES.gilroy,
            }}
            fullWidth
            size='small'
            disabled={false}
            value={bookingId}
            onChange={handleChange}
            placeholder='booking Id'
            variant='outlined'
          />
          <Stack direction='row' spacing={2}>
            <Button
              sx={{
                fontFamily: FONT_FAMILIES.gilroy,
              }}
              variant='outlined'
              size='small'
              onClick={() => {
                dispatch(setBookingId(''));
              }}
            >
              Clear
            </Button>
            <Button
              sx={{
                fontFamily: FONT_FAMILIES.gilroy,
              }}
              variant='contained'
              size='small'
              disabled={!bookingId}
              onClick={() => {
                dispatch(getDocument());
              }}
            >
              Get Document
            </Button>
          </Stack>
        </Stack>
      </Grid>
    </Grid>
  );
}

export default SearchDocument;
