import { Alert, Snackbar } from '@mui/material';
import { API_STATUS } from 'src/static/api';
import { PALETTE_TYPES } from 'src/static/styles';

function APIAlert({ loadingStatus, onClose, successMessage, errorMessage }) {
  return (
    <>
      {successMessage && (
        <Snackbar open={loadingStatus === API_STATUS.done} autoHideDuration={6000} onClose={() => onClose()}>
          <Alert onClose={() => onClose()} variant='filled' severity={PALETTE_TYPES.success} sx={{ width: '100%' }}>
            {successMessage}
          </Alert>
        </Snackbar>
      )}
      {errorMessage && (
        <Snackbar open={loadingStatus === API_STATUS.failed} autoHideDuration={6000} onClose={() => onClose()}>
          <Alert onClose={() => onClose()} variant='filled' severity={PALETTE_TYPES.error} sx={{ width: '100%' }}>
            {errorMessage}
          </Alert>
        </Snackbar>
      )}
    </>
  );
}

export default APIAlert;
