import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
// import createToken from 'src/services/create-token';
import { API_STATUS, ERROR_MESSAGES } from 'src/static/api';
// import getDocumentAPI from 'src/services/get-document';

const initialState = {
  bookingId: '',
  transportDocument: null,
  confirmationNo: '',
  status: API_STATUS.initial,
  errorMessage: ERROR_MESSAGES[500],
};

export const getDocument = createAsyncThunk('transport/getDocument', async () => {
  try {
    // const token = await createToken();
    // const requestBody = { bookingId };
    // const response = await getDocumentAPI({ token, requestBody });
    // return response;
    return new Promise((resolve) => {
      setTimeout(() => {
        resolve();
      }, 3000);
    });
  } catch (err) {
    throw new Error(err.message);
  }
});

export const transportDocumentSlice = createSlice({
  name: 'transportDocument',
  initialState,
  reducers: {
    setBookingId: (state, action) => {
      state.bookingId = action.payload;
    },
    setConfirmationNo: (state, action) => {
      state.confirmationNo = action.payload;
    },
    updateLoadingStatus: (state, action) => {
      state.status = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(getDocument.pending, (state) => {
        state.status = API_STATUS.loading;
        state.errorMessage = '';
      })
      .addCase(getDocument.fulfilled, (state, action) => {
        state.status = API_STATUS.done;
        state.transportDocument = action.payload;
      })
      .addCase(getDocument.rejected, (state, action) => {
        state.status = API_STATUS.failed;
        state.errorMessage = action.payload;
      });
  },
});

export const transportDocumentSelector = (state) => state.transportDocument;
export const { setBookingId, setConfirmationNo } = transportDocumentSlice.actions;
export default transportDocumentSlice.reducer;
