import { Backdrop, CircularProgress } from '@mui/material';
import { API_STATUS } from 'src/static/api';

function CustomLoader({ loadingStatus }) {
  return (
    <Backdrop sx={{ zIndex: (theme) => theme.zIndex.drawer + 1 }} open={loadingStatus === API_STATUS.loading}>
      <CircularProgress />
    </Backdrop>
  );
}

export default CustomLoader;
