import { useTheme } from '@emotion/react';
import { CheckCircle } from '@mui/icons-material';
import { Button, Card, Box, CardContent, IconButton, Popover, Typography } from '@mui/material';
import { useState } from 'react';
import { REQUESTED_STATUS_COLORS_BY_KEY, STATUS_TEXT, TRANSITION_LIST_BY_KEY } from 'src/static/mobile-key-status';
import { CENTER_STYLES, FONT_FAMILIES } from 'src/static/styles';
import { getFirstLetterCapitalizedRestInLowerCase } from 'src/utils/helper';

const checkBoxDesign = ({ selectedStatus, currentStatus }) => ({
  display: 'flex',
  justifyContent: 'space-around',
  cursor: 'pointer',
  alignItems: 'center',
  px: 0.5,
  py: 1,
  m: 1,
  '&:hover': {
    color: `${REQUESTED_STATUS_COLORS_BY_KEY[currentStatus]}.main`,
    backgroundColor: `${REQUESTED_STATUS_COLORS_BY_KEY[currentStatus]}.light`,
  },
  ...(selectedStatus
    ? {
        color: `${REQUESTED_STATUS_COLORS_BY_KEY[currentStatus]}.main`,
        backgroundColor: `${REQUESTED_STATUS_COLORS_BY_KEY[currentStatus]}.light`,
      }
    : {}),
});

function RequestedStatusCol({ id, status, row, handleSubmitStatus }) {
  const [anchorEl, setAnchorEl] = useState(null);
  const [display, setDisplay] = useState(false);

  const [transitionList, setTransitionList] = useState([]);
  const [selectedStatus, setSelectedStatus] = useState('');
  const open = Boolean(anchorEl);

  const theme = useTheme();

  const handleOpen = (event) => {
    setTransitionList(TRANSITION_LIST_BY_KEY[status]);
    setAnchorEl(event.currentTarget);
  };

  const handleSelect = (data) => {
    if (data === selectedStatus) {
      setSelectedStatus('');
    } else {
      setSelectedStatus(data);
    }
  };

  const handleClose = () => {
    setAnchorEl(null);
  };

  const handleReset = () => {
    setAnchorEl(null);
    setSelectedStatus('');
  };

  const statusText = getFirstLetterCapitalizedRestInLowerCase(status);

  return (
    <>
      <Typography variant='body2' sx={{ fontFamily: FONT_FAMILIES.gilroy }}>
        {statusText}(
        <Typography
          component='span'
          color='primary'
          sx={{
            cursor: 'pointer',
          }}
          onClick={(event) => handleOpen(event)}
        >
          change
        </Typography>
        )
      </Typography>
      <Popover
        id={id}
        open={open}
        anchorEl={anchorEl}
        onClose={handleClose}
        anchorOrigin={{
          vertical: 'bottom',
          horizontal: 'left',
        }}
      >
        <Box sx={{ minWidth: 275, p: 1, backgroundColor: 'neutral.100' }}>
          <Typography
            variant='h5'
            component='div'
            sx={{ fontFamily: 'TfArrow', fontWeight: 'bold', textAlign: 'center', my: 1 }}
          >
            Change Status
          </Typography>
          <Card sx={{ mx: 1 }}>
            <CardContent>
              <Box sx={{ ...CENTER_STYLES, justifyContent: 'space-around' }}>
                <Typography sx={{ fontSize: '1rem' }} color='text.secondary'>
                  Current Status :
                </Typography>
                <Typography
                  sx={{
                    fontSize: '1rem',
                    textAlign: 'center',
                    px: 0.5,
                    py: 0.25,
                    color: `${REQUESTED_STATUS_COLORS_BY_KEY[status]}.main`,
                    backgroundColor: `${REQUESTED_STATUS_COLORS_BY_KEY[status]}.light`,
                  }}
                >
                  {statusText}
                </Typography>
              </Box>
              {transitionList.map((data) => {
                return (
                  <Box
                    onClick={() => handleSelect(data)}
                    onMouseLeave={() => {
                      setDisplay(false);
                    }}
                    onMouseEnter={() => {
                      setDisplay(true);
                    }}
                    key={data}
                    sx={checkBoxDesign({ selectedStatus, currentStatus: data })}
                  >
                    <Box>{STATUS_TEXT[data]}</Box>
                    <IconButton
                      sx={{
                        border: `1px solid ${theme.palette.neutral[500]}`,
                        padding: 0,
                      }}
                      size='small'
                    >
                      <CheckCircle
                        sx={{
                          visibility: display || selectedStatus ? 'inherit' : 'hidden',
                          color: `${REQUESTED_STATUS_COLORS_BY_KEY[data]}.main`,
                          '&:hover': {},
                        }}
                      />
                    </IconButton>
                  </Box>
                );
              })}
            </CardContent>
          </Card>

          <Box sx={{ width: '100%', px: 0.5, mt: 1 }}>
            <Button sx={{ width: '50%' }} onClick={handleReset}>
              Cancel
            </Button>
            <Button
              sx={{ width: '50%' }}
              onClick={() => {
                handleSubmitStatus({ status: selectedStatus, id, row });
                handleReset();
              }}
              variant='contained'
            >
              Done
            </Button>
          </Box>
        </Box>
      </Popover>
    </>
  );
}

export default RequestedStatusCol;
