import {
  Box,
  Button,
  Card,
  CardContent,
  Container,
  FormLabel,
  Grid,
  Stack,
  TextField,
  Typography,
} from '@mui/material';
import { useDispatch, useSelector } from 'react-redux';
import APIAlert from 'src/components/shared/api-alert';
import CustomLoader from 'src/components/shared/loader';
import {
  initiateTransfer,
  transferDocumentSelector,
  updateBookingId,
  updateLoadingStatus,
} from 'src/redux/transfer-document';
import { API_STATUS, SUCCESS_MESSAGES } from 'src/static/api';
import { FONT_FAMILIES } from 'src/static/styles';

function TransferDocument() {
  const { bookingId, status, errorMessage } = useSelector(transferDocumentSelector);
  const dispatch = useDispatch();

  const handleChange = (event) => {
    dispatch(updateBookingId(event.target.value.trim()));
  };

  return (
    <Box
      component='main'
      sx={{
        flexGrow: 1,
        py: 8,
      }}
    >
      <Container maxWidth={false}>
        <Card>
          <CardContent>
            <Grid container spacing={2}>
              <Grid item xs={3}>
                <Stack spacing={2} sx={{ m: 1 }}>
                  <Typography variant='h5'>Transfer Document</Typography>
                  <FormLabel>Enter Booking Id : </FormLabel>
                  <TextField
                    sx={{
                      fontFamily: FONT_FAMILIES.gilroy,
                    }}
                    fullWidth
                    size='small'
                    disabled={false}
                    value={bookingId}
                    onChange={handleChange}
                    placeholder='booking Id'
                    variant='outlined'
                  />
                  <Stack direction='row' spacing={2}>
                    <Button
                      sx={{
                        fontFamily: FONT_FAMILIES.gilroy,
                      }}
                      variant='outlined'
                      size='small'
                      onClick={() => {
                        dispatch(updateBookingId(''));
                      }}
                    >
                      Clear
                    </Button>
                    <Button
                      sx={{
                        fontFamily: FONT_FAMILIES.gilroy,
                      }}
                      variant='contained'
                      size='small'
                      disabled={!bookingId}
                      onClick={() => {
                        dispatch(initiateTransfer());
                      }}
                    >
                      Initiate Transfer
                    </Button>
                  </Stack>
                </Stack>
              </Grid>
            </Grid>
          </CardContent>
        </Card>
      </Container>
      <CustomLoader loadingStatus={status} />
      <APIAlert
        loadingStatus={status}
        onClose={() => {
          dispatch(updateLoadingStatus(API_STATUS.initial));
        }}
        errorMessage={errorMessage}
        successMessage={SUCCESS_MESSAGES.initiateDocument}
      />
    </Box>
  );
}

export default TransferDocument;
