import { Box, Divider, Drawer, IconButton, Stack } from '@mui/material';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import { styled, useTheme } from '@mui/styles';
import logo from 'src/assets/images/logo.png';

import { CENTER_STYLES, DRAWER_WIDTH } from 'src/static/styles';
import { MobileFriendly } from '@mui/icons-material';
import { ROUTES } from 'src/static/routes';
import NavItem from './nav-item';

const DrawerHeader = styled('div')(({ theme }) => ({
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'flex-end',
  padding: theme.spacing(0, 1),
  ...theme.mixins.toolbar,
}));

const items = [
  {
    href: ROUTES.dashboard,
    icon: <MobileFriendly sx={{ fontSize: 1 }} />,
    title: 'Mobile Key Status',
  },
  // {
  // 	href: ROUTES.transferDocument,
  // 	icon: <DocumentScanner sx={{ fontSize: 1 }} />,
  // 	title: 'Transfer Document',
  // },
  // {
  //   href: ROUTES.transportDocument,
  //   icon: <EmojiTransportation sx={{ fontSize: 1 }} />,
  //   title: 'Transport Document',
  // },
];

function DashboardSidebar(props) {
  const { open, onClose } = props;
  const theme = useTheme();

  const content = (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        height: '100%',
      }}
    >
      <div>
        <Box sx={{ px: 2 }}>
          <Box
            sx={{
              backgroundColor: 'background.box',
              cursor: 'pointer',
              ...CENTER_STYLES,
            }}
          >
            <img alt='Logo' src={logo} layout='intrinsic' width={130} height={40} />
          </Box>
        </Box>
      </div>
      <Divider
        sx={{
          my: 3,
        }}
      />
      <Box sx={{ flexGrow: 1 }}>
        {items.map((item) => (
          <NavItem key={item.title} icon={item.icon} href={item.href} title={item.title} />
        ))}
      </Box>
      <Divider />
    </Box>
  );

  return (
    <Drawer
      anchor='left'
      onClose={onClose}
      open={open}
      PaperProps={{
        sx: {
          backgroundColor: 'secondary.main',
          color: 'secondary.contrastText',
          width: DRAWER_WIDTH,
          '& .MuiDrawer-paper': {
            width: DRAWER_WIDTH,
            boxSizing: 'border-box',
          },
        },
      }}
      sx={{
        zIndex: (themeCurrent) => themeCurrent.zIndex.appBar + 100,
      }}
      variant='persistent'
    >
      <DrawerHeader>
        <Stack direction='row-reverse'>
          <IconButton onClick={onClose}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </Stack>
      </DrawerHeader>
      {content}
    </Drawer>
  );
}

export default DashboardSidebar;
