import { CONTENT_TYPES } from 'src/static/api';

export const getHeaders = () => {
  return {
    'Content-Type': CONTENT_TYPES.json,
    'Ocp-Apim-Subscription-Key': process.env.REACT_APP_SUBSCRIPTION_KEY,
  };
};

export const getBasicAuthorizationHeader = () => {
  return {
    Authorization: `Basic ${process.env.REACT_APP_BASIC_TOKEN}`,
  };
};

export const getBasicWithDefaultHeaders = () => {
  return { ...getHeaders(), ...getBasicAuthorizationHeader() };
};

export const getBearerAuthorizationHeader = (token) => {
  return {
    Authorization: `Bearer ${token}`,
  };
};

export const getBearWithDefaultHeaders = (token) => {
  return { ...getHeaders(), ...getBearerAuthorizationHeader(token) };
};

export const getTokenHeader = (token) => {
  return {
    Token: token,
  };
};
