import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import createToken from 'src/services/create-token';
import { API_STATUS, ERROR_MESSAGES } from 'src/static/api';
import initiateTransferAPI from 'src/services/initaite-transfer';

const initialState = {
  bookingId: '',
  status: API_STATUS.initial,
  errorMessage: ERROR_MESSAGES[500],
};

export const initiateTransfer = createAsyncThunk('transferDocument/initiateTransfer', async (bookingId) => {
  try {
    const token = await createToken();
    const requestBody = { bookingId };
    await initiateTransferAPI({ token, requestBody });
  } catch (err) {
    throw new Error(err.message);
  }
});

export const transferDocumentSlice = createSlice({
  name: 'transferDocument',
  initialState,
  reducers: {
    updateBookingId: (state, action) => {
      state.bookingId = action.payload;
    },
    updateLoadingStatus: (state, action) => {
      state.status = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(initiateTransfer.pending, (state) => {
        state.status = API_STATUS.loading;
        state.errorMessage = '';
      })
      .addCase(initiateTransfer.fulfilled, (state) => {
        state.status = API_STATUS.done;
      })
      .addCase(initiateTransfer.rejected, (state, action) => {
        state.status = API_STATUS.failed;
        state.errorMessage = action.payload;
      });
  },
});

export const transferDocumentSelector = (state) => state.transferDocument;
export const { updateBookingId, updateLoadingStatus } = transferDocumentSlice.actions;
export default transferDocumentSlice.reducer;
